/* checkbox */
.container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 26px;
  padding-right: 9px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  @include mobile {
    font-size: 23px;
  }
}

/* Hide the browser's default checkbox */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  border-radius: 50%;
  top: 10px;
  right: 9px;
  height: 32px;
  width: 32px;
  background-color: transparent;
  @include mobile {
    top: 4px;
    right: 1px;
  }
}

/* When the checkbox is checked, add an orange background */
.container input:checked ~ .checkmark {
  background-color: $themeColor;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container .checkmark:after {
  left: 10px;
  top: 4px;
  width: 7px;
  height: 14px;
  border: solid white;
  border-width: 0 6px 6px 0;
  transform: rotate(45deg);
}
.one {
  position: relative;
  right: 38px;
  top: 4px;
}
