/* splash */
.splash {
  background: url(../pic/Work/splash/background.png) no-repeat,
    linear-gradient(#ff9000, #d85927);
  background-position: center;
  width: 1920px;
  height: 1080px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @include mobile {
    height: 100vh;
    width: 100vw;
    background-position: bottom;
    background-size: cover;
  }
  @include tablet {
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-size: cover;
  }
  // @include desktop1 {
  //   height: 100vh;
  //   width: 100vw;
  //   background-position: center;
  //   background-size: cover;
  // }
  @include desktop2 {
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-size: cover;
  }
  @include desktop3 {
    background: url(/static/media/background2x.a3557525.png) no-repeat,
      linear-gradient(#ff9000, #d85927);
    background-position: center;
    background-size: cover;
    width: 100vw;
    height: 100vh;
  }
}
.splash-logo {
