$mobile: 420px;
$tablet: 770px;
$desktop1: 1420px;
$desktop2: 1920px;
$iphonex: 375px;
$themeColor: #f78408;
@mixin mobile {
  @media (max-width:#{$mobile}) {
    @content;
  }
}
@mixin tablet {
  @media (max-width:#{$tablet}) and (min-width:#{$mobile}) {
    @content;
  }
}
@mixin desktop1 {
  @media (max-width:#{$desktop1}) and (min-width:#{$tablet}) {
    @content;
  }
}
@mixin desktop2 {
  @media (max-width:#{$desktop2}) and (min-width:#{$tablet}) {
    @content;
  }
}
@mixin desktop3 {
  @media (min-width:#{$desktop2}) {
    @content;
  }
}
@mixin iphonex {
  @media (max-width:#{400px}) and (min-width:#{$iphonex}) {
    @content;
  }
}
